export default ".mainExternal {\n  width: var(--semantic-responsive-container);\n  box-sizing: border-box;\n  padding: var(--semantic-responsive-container-spacing);\n  margin: auto;\n\n  &:focus-visible {\n    outline: none;\n  }\n}\n\n.tabIndexNoOutline:focus {\n  outline: none;\n}\n\n.miniNav {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: var(--spacing-m) 0;\n}\n\n.article {\n  width: var(--semantic-responsive-article);\n  margin: auto;\n}\n\n.marginTopL {\n  margin-top: var(--spacing-l);\n}\n\n.desciptionList {\n  display: grid;\n  gap: var(--spacing-xs);\n  grid-template-columns: 1fr 1fr;\n}\n\n.repeatingFields {\n  display: flex;\n  flex-direction: column;\n  padding-left: 0;\n  gap: var(--spacing-m);\n  list-style: none;\n}\n\n.addNewButton {\n  max-width: max-content;\n}\n\n.editModal {\n  width: 100%;\n}\n\n.editModalContent {\n  display: flex;\n  flex-direction: column;\n  gap: var(--spacing-m);\n}\n\n.flexStartRow {\n  display: flex;\n  justify-content: flex-start;\n  gap: var(--spacing-m);\n}\n\n.editModalAdressFields {\n  display: flex;\n  gap: var(--spacing-m);\n\n  dt {\n    color: var(--palette-graphite-70);\n  }\n}\n\n.cardContent div:first-child {\n  width: 100%;\n}\n\n.panel {\n  margin: var(--spacing-l) auto;\n}\n\n.panelContent {\n  display: flex;\n  flex-direction: row;\n  gap: var(--spacing-l);\n\n  @media (width <= 640px) {\n    flex-direction: column;\n    gap: var(--spacing-s);\n  }\n}\n"